'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import BookingFilters from '@/components/admin/bookings/BookingFilters';
import BookingTable from '@/components/admin/bookings/BookingTable';
import BookingDetailDialog from '@/components/admin/bookings/BookingDetailDialog';
import BookingCalendar from '@/components/admin/bookings/BookingCalendar';
import socket from '@/lib/socket';
import { Booking } from '@/types';

export default function BookingAdminPage() {
    const [bookings, setBookings] = useState<Booking[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [searchQuery, setSearchQuery] = useState('');
    const [filterStatus, setFilterStatus] = useState('all');

    const [selectedBooking, setSelectedBooking] = useState<Booking | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);

    const [selectedDateFilter, setSelectedDateFilter] = useState<string | null>(null);

    const fetchBookings = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/bookings');
            setBookings(response.data);
        } catch (error) {
            toast.error("Gagal memuat data booking.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchBookings(); }, []);

    const filteredBookings = bookings.filter(item => {
        const matchSearch = item.kode_booking.toLowerCase().includes(searchQuery.toLowerCase()) || 
                            item.user?.name.toLowerCase().includes(searchQuery.toLowerCase());
        const matchStatus = filterStatus === 'all' || item.status_booking_id.toString() === filterStatus;
        const matchDate = !selectedDateFilter || item.tanggal_booking === selectedDateFilter;
        
        return matchSearch && matchStatus && matchDate;
    });

    const handleApprove = async (id: number) => {
        if(!confirm("Konfirmasi booking ini?")) return;
        setIsProcessing(true);
        try {
            const response = await axios.patch(`/api/bookings/${id}/status`, { status_booking_id: 3 });
            toast.success("Booking berhasil dikonfirmasi!");
            try { socket.emit('booking:confirmed', response.data); } catch(e) {}
            setIsDialogOpen(false);
            fetchBookings();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal konfirmasi.");
        } finally {
            setIsProcessing(false);
        }
    };

    const handleReject = async (id: number) => {
        if(!confirm("Tolak booking ini? Uang muka (jika ada) harus dikembalikan manual.")) return;
        setIsProcessing(true);
        try {
            const response = await axios.patch(`/api/bookings/${id}/cancel`); 
            toast.success("Booking dibatalkan.");
            try { socket.emit('booking:rejected', response.data); } catch(e) {}
            setIsDialogOpen(false);
            fetchBookings();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal membatalkan.");
        } finally {
            setIsProcessing(false);
        }
    };

    return (
        <div className="space-y-6">
            <div>
                <h1 className="text-2xl font-bold text-gray-900">Booking Lapangan</h1>
                <p className="text-gray-500 text-sm">Validasi pembayaran dan kelola jadwal lapangan.</p>
            </div>

            <BookingFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                filterStatus={filterStatus}
                setFilterStatus={setFilterStatus}
            />

            <div className="mt-4 space-y-3">
                <BookingCalendar bookings={bookings} onSelectDate={setSelectedDateFilter} />

                {selectedDateFilter && (
                    <div className="flex items-center justify-between bg-white p-3 rounded-md border">
                        <div className="text-sm">Filter tanggal: <span className="font-medium">{new Date(selectedDateFilter).toLocaleDateString('id-ID')}</span></div>
                        <div>
                            <button onClick={() => setSelectedDateFilter(null)} className="text-sm text-gray-500 hover:text-gray-700">Hapus filter</button>
                        </div>
                    </div>
                )}
            </div>

            <BookingTable 
                bookings={filteredBookings}
                loading={loading}
                onView={(booking) => {
                    setSelectedBooking(booking);
                    setIsDialogOpen(true);
                }}
            />

            <BookingDetailDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                booking={selectedBooking}
                onApprove={handleApprove}
                onReject={handleReject}
                isProcessing={isProcessing}
            />
        </div>
    );
}